/* styles imported from navbar/index.css */
.navbar {
  width: 100%;
  height: 36px;
  /* vertical-align: bottom; */
  border: 1px solid black;
}

.brand {
  vertical-align: bottom;
}

.navbar div {
  /* display: inline; */
  padding-left: 5px;
  padding-right: 5px;
}

.navbar-items {
  margin: 0;
  padding: 0;
  display: inline-block;
}

/* list-style-type: none; */
/*
.navbar-items span {
  display: inline-block;
} */
.navbar-items div {
  display: inline-block;
}

/* .navbar-items */
.navbar-lhs {
  float: left;
}

/* .navbar-items */
.navbar-center {
  margin-left: 50px;
  margin-right: 50px;
}

/* .navbar-items */
.navbar-rhs {
  float: right;
}

.navbar-btn {
  /* display: inline; */
  border: 1px solid black;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-top: 2px;
  margin-bottom: 2px;
  border-radius: 0;
}

.navbar-text {
  vertical-align: top;
  padding-top: 4px;
}

#navbar_home_btn:hover {
  cursor: pointer;
}

#navbar_glossary_btn {
  background-color: orange;
}

.navbar-glossary-popup {
  /* display: none; */
  position: absolute;
  background-color: white;
  top: 40px;
  left: 10%;
  height: 80%;
  width: 80%;
  z-index: 2;
}

